@import url('https://fonts.googleapis.com/css2?family=M+PLUS+1p&family=Noto+Sans+JP&display=swap');

* {
  font-family: 'M PLUS 1p', 'Noto Sans JP', sans-serif;
  color: #435057;
}

body {
  background: url('../public/cat.png'), rgb(253,219,93);
  background-size: 5%;
  background-blend-mode: multiply;
  background-repeat: space;

  padding: 10px 5%;
}

.wrapper {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.header {
  flex-basis: 100%;
}

.item {
  flex-basis: 100%;
  height: 40px;
  /* line-height: 40px; */

  margin-bottom: 10px;
  font-weight: bold;
}

.doneat {
  color: #ed7a9c;
  font-size: 8;
}

input[type=checkbox] {
  display: none;
}

.checkbox {
  box-sizing: border-box;
  cursor: pointer;
  display: inline-block;
  padding: 5px 30px;
  position: relative;
  width: auto;
}

.checkbox::before {
  background: #fff;
  border: 1px solid #231815;
  border-radius: 5px;
  content: '';
  display: block;
  height: 16px;
  left: 5px;
  margin-top: -8px;
  position: absolute;
  top: 50%;
  width: 16px;
}

.checkbox::after {
  border-right: 3px solid #ed7a9c;
  border-bottom: 3px solid #ed7a9c;
  content: '';
  display: block;
  height: 9px;
  left: 10px;
  margin-top: -7px;
  opacity: 0;
  position: absolute;
  top: 50%;
  transform: rotate(45deg);
  width: 5px;
}

input[type=checkbox]:checked + .checkbox {
  text-decoration: line-through;
}

input[type=checkbox]:checked + .checkbox::after {
  opacity: 1;
}